import React from 'react';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import Slide from '@material-ui/core/Slide';

const SlideTransition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} timeout={{enter: 500, exit: 500}} />;
});

const SlideUpTransition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} timeout={{enter: 500, exit: 500}} />;
});

export default function DraggableDialog(props) {

  function PaperComponent(props2) {
    return (
      <Draggable bounds={{left: 0}} onStart={props.transferredControl.onStart} onStop={props.transferredControl.onStop} axis="x" handle="#draggable-dialog-title" cancel={'[class*="MuiDialogContent-root"]'}>
        <Paper {...props2} />
      </Draggable>
    );
  }

  return (
      <Dialog
        {...props.transferredControl}
        style={props.style} 
        TransitionComponent={props.transitionType === 'Slide' ?
                     SlideTransition :
                     props.transitionType === 'SlideUp' ?
                     SlideUpTransition :
                    undefined}
        PaperComponent={PaperComponent}>
          {props.children}
          <DialogTitle style={{position: "absolute", left: 0, top: 0, width: 32, height: "100%", cursor: 'move' }} id="draggable-dialog-title">
          </DialogTitle>
      </Dialog>
  );
}